.clock {
  position: relative;
  border-radius: 50%;
  background-color: var(--clock-backgound-color);
  -webkit-box-shadow: inset -5px 3px 20px 2px rgba(10, 11, 10, .6);
          box-shadow: inset -5px 3px 20px 2px rgba(10, 11, 10, .6);
  -webkit-transform: rotate(180deg);
      -ms-transform: rotate(180deg);
          transform: rotate(180deg);
}
.clock_needle {
  position: absolute;
  transition-name: transform;
}
.clock_needleRotate {
  position: absolute;
}



